label {
    width: 200px;
    display: flex;

 
  }
.forminput {
    border: 3px solid rgb(23, 180, 219);
    border-radius: 7px;
  }

.forminput:focus {
    background-color: lightblue;
}




  .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  .switch .inputswitch { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .inputswitch:checked + .slider {
    background-color: #2196F3;
  }
  
  .inputswitch:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  .inputswitch:checked + .slider:before {
   
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }



.settingform {
    padding-top: 70px;
    
}


